@mixin numbered-title {
  text-transform: uppercase;
  font-size: var(--fs-500);
  font-family: var(--ff-sans-cond);
  letter-spacing: 4.72px;
  span {
    margin-right: 0.5em;
    font-weight: 700;
    color: rgba(255, 255, 255, 0.2);
  }
}

@mixin underlined-menu($underline-gap: 1rem, $bottom: right) {
  > * {
    cursor: pointer;
    padding: $underline-gap 0;
    border: 0;
    border-#{$bottom}: 0.2rem solid transparent;
  }
  > *:hover {
    border-color: rgba(255, 255, 255, 0.2);
  }
  > *:focus {
    color: var((--clr-white));
    border-color: rgba(255, 255, 255, 1);
  }
  *[aria-selected="true"] {
    color: var((--clr-white));
    border-color: rgba(255, 255, 255, 1);
  }
}

// screen reader only
@mixin sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

@mixin dot-menu {
  > * {
    display: inline-block;
    padding: 0.4rem;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    aspect-ratio: 1;
    background-color: rgba(255, 255, 255, 0.2);
    transition: background-color 350ms ease-in-out;
  }
  > *:not(:first-child) {
    margin-left: 1.5rem;
  }
  > *:hover {
    background-color: rgba(255, 255, 255, 0.5);
  }
  > *:focus {
    color: var((--clr-white));
    background-color: rgba(255, 255, 255, 1);
  }
  *[aria-selected="true"] {
    color: var((--clr-white));
    background-color: rgba(255, 255, 255, 1);
  }
}

@mixin step-menu($margin-left: 1.5rem, $margin-bottom: 0) {
  > * {
    color: var(--clr-white);
    font-family: var(--ff-serif);
    border: 1px solid rgba(255, 255, 255, 0.3);
    background: none;
    cursor: pointer;
    padding-inline: 1.2rem;
    aspect-ratio: 1;
    border-radius: 50%;
    font-size: 1.2rem;
  }
  > *:not(:first-child) {
    margin-left: $margin-left;
  }
  > *:not(:last-child) {
    margin-bottom: $margin-bottom;
  }
  > *:hover {
    border-color: var(--clr-white);
  }
  > *:focus {
    color: var((--clr-black));
    border-color: var(--clr-white);
    background: var(--clr-white);
  }
  *[aria-selected="true"] {
    color: var((--clr-black));
    border-color: var(--clr-white);
    background: var(--clr-white);
  }
}
